<template>
  <div class="start d-flex flex-column justify-center align-center">
    <h1 class="text text-h4 text-center mb-10" style="color: #ffffff">Ты любишь меня?</h1>
    <div>
      <v-btn color="#ff75c3" large @click="Yes" class="mr-10">Да</v-btn>
      <v-btn color="#ff0000" large @click="No">Нет</v-btn>
    </div>
  </div>
</template>

<style>
.start{
  width: 100vw;
  height: 100vh;
  background: #1d1d1d;
}
</style>

<script>
export default {
  methods:{
    Yes(){
      this.$emit('yes');
    },
    No(){
      this.$emit('no');
    }
  }
}
</script>