<template>
  <v-app>
    <v-main>
      <Start v-if="state === null" @yes="Yes" @no="No"></Start>
      <Yes v-if="state"></Yes>
      <No v-else></No>
    </v-main>
  </v-app>
</template>

<script>
// import HelloWorld from './components/HelloWorld';
import Start from "./components/Start";
import Yes from "./components/Yes";
import No from "./components/No";

export default {
  name: 'App',

  components: {
    No,
    Yes,
    Start
  },

  data: () => ({
    state: null
  }),
  methods: {
    Yes(){
      this.state = true;
      console.log("yes");
    },
    No(){
      this.state = false;
      console.log("No");
    }
  }
};
</script>
